import React from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import styled from 'styled-components';

const DashboardContainer = styled.div`
  background-color: #1a1a2e;
  color: #ffffff;
  padding: 20px;
  border-radius: 10px;
  max-width: 800px;
  margin: 0 auto;
`;

const NewsHeader = styled.h2`
  color: #4cd137;
  margin-bottom: 20px;
`;

const NewsContent = styled.p`
  margin-bottom: 20px;
`;

const PriceInfo = styled.div`
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
`;

const PriceItem = styled.div`
  text-align: center;
`;

const NewsDashboard = ({ newsData }) => {
  const { en, firstPrice, suggestions } = newsData;

  const chartOptions = {
    chart: {
      type: 'line',
      backgroundColor: '#2c3e50'
    },
    title: {
      text: 'Price Comparison',
      style: { color: '#ecf0f1' }
    },
    xAxis: {
      categories: Object.keys(firstPrice),
      labels: { style: { color: '#ecf0f1' } }
    },
    yAxis: {
      title: { text: 'Price', style: { color: '#ecf0f1' } },
      labels: { style: { color: '#ecf0f1' } }
    },
    series: [{
      name: 'Price',
      data: Object.values(firstPrice),
      color: '#3498db'
    }],
    legend: { itemStyle: { color: '#ecf0f1' } }
  };

  return (
    <DashboardContainer>
      <NewsHeader>Breaking News</NewsHeader>
      <NewsContent>{en}</NewsContent>
      <PriceInfo>
        {Object.entries(firstPrice).map(([symbol, price]) => (
          <PriceItem key={symbol}>
            <h3>{symbol}</h3>
            <p>{price}</p>
          </PriceItem>
        ))}
      </PriceInfo>
      <HighchartsReact highcharts={Highcharts} options={chartOptions} />
    </DashboardContainer>
  );
};

export default NewsDashboard;